"use client";

import { useEffect, useState } from "react";
import { cn } from "@/lib/utils";

interface HeaderWrapperProps {
  children: React.ReactNode;
}

export function HeaderWrapper({ children }: HeaderWrapperProps) {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isHovered, setIsHovered] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div
      className={cn(
        "sticky top-0 z-50 flex flex-col items-center px-4 py-4 shadow-md transition-colors duration-300",
        isScrolled || isHovered ? "bg-brand-pink text-brand-red" : "bg-black"
      )}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {children}
    </div>
  );
}
